<template>
  <div id="app">
    <div class="navbar navbar-dark">
  <!--Left Link-->
      <a href="#" class="left" style="font-weight:900px;"><strong>Ash-Editor</strong></a>  
  <!--Right Links-->
    <a href="#" class="right"><button @click="addContent()" class="pure-material-button-contained">Run</button></a>
