    <div class="parent">
      <m-ace-editor height="300px" width="100%" v-model="htmlCode" :line-number="True" mode="html" theme="monokai"></m-ace-editor>
      <m-ace-editor height="300px" width="100%" v-model="cssCode" :line-number="True" mode="css" theme="monokai"></m-ace-editor>
